var SNS = require("SNS");
var G = require("G");
cc.Class({
    extends: cc.Component,

    properties: {
        // foo: {
        //     // ATTRIBUTES:
        //     default: null,        // The default value will be used only when the component attaching
        //                           // to a node for the first time
        //     type: cc.SpriteFrame, // optional, default is typeof default
        //     serializable: true,   // optional, default is true
        // },
        // bar: {
        //     get () {
        //         return this._bar;
        //     },
        //     set (value) {
        //         this._bar = value;
        //     }
        // },
        OverUI: cc.Prefab,

        scoreLabel: cc.Label,

    },

    // LIFE-CYCLE CALLBACKS:

    // onLoad () {},

    start() {
        SNS.showAD();
        this.scoreLabel.string = "" + G.game.gametime;
    },


    ad() {
        let self = this;
        SNS.hideAD();
        SNS.showVideo(() => {
            G.game.over = false;
            console.log("添加这之前的分数"+G.game.gametime);
            if(G.game.gametime>0){
                G.game.gametime += 20;
               
                G.game.addhp();
            }else{
                G.game.gametime = 20;
            }
            console.log("添加这之后的分数"+G.game.gametime);
            self.node.destroy();
        }, () => {
            G.Tip.show("看完广告获得奖励");
        });
        return;    
    
        cc.log("开启分享");
        SNS.share("快来帮我一下~", () => {

            G.game.over = false;
            G.game.gametime += 20;
            G.game.addhp();

            this.node.destroy();

        }, () => {

            G.Tip.show("分享失败");
        });
    },

    jump() {
        SNS.hideAD();
        
        let node = cc.instantiate(this.OverUI);

        node.parent = this.node.parent;

        this.node.destroy();
    },
    // update (dt) {},

});